@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap');

body {
  background-color: #121212;
  color: #E0E0E0;
  font-family: 'Inter', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  text-align: center;
}

.container {
  max-width: 600px;
  padding: 40px;
  border: 1px solid #333;
  border-radius: 12px;
  background-color: #1E1E1E;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.25);
}

.header h1 {
  font-size: 2.5em;
  color: #FFFFFF;
  margin-bottom: 0.25em;
}

.subtitle {
  font-size: 1.2em;
  color: #BB86FC; /* Warna ungu khas AWS */
  margin-top: 0;
  margin-bottom: 1.5em;
}

.main-content p {
  font-size: 1.1em;
  line-height: 1.6;
  color: #B0B0B0;
}

.main-content strong {
  color: #FFFFFF;
  font-weight: 700;
}

.footer {
  margin-top: 2em;
  font-size: 0.9em;
  color: #757575;
}

.footer p {
    margin: 0.5em 0;
}